















































import Vue from 'vue';
import { Component, Prop, Watch, Emit } from 'vue-property-decorator';

@Component
export default class AppExamCard extends Vue {
  $vuetify: any;

  private alerts = {
    pwa: true,
    errors: true,
    swipe: true,
    spaces: true
  };

  get isPhone(): boolean {
    return this.$vuetify.breakpoint.name === 'xs';
  }
}
